.list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.link {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
b {
  margin-top: 0;
  margin-bottom: 0;
}

ul,
ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

img {
  width: 100%;
  display: block;
}

body {
  font-family: var(--main-font), 'Open Sans' sans-serif;
  letter-spacing: 0.08em;
}
